import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'

const initialState = {
    message: {
        data: "",
        time: '',
        city: '',
        courseName: '',
        defaultCourse: null
    }
}

const generatedommentSlice = createSlice({
    name: 'generatedomment',
    initialState,
    reducers: {
        generatomment: (state, action) => {
            state.message = action.payload
        }
    }})

    export const { generatomment } = generatedommentSlice.actions;
    export const generatedommentlReducer = generatedommentSlice.reducer